import { SectionInstruction, getInchAndCm } from "@tanyoknits/shared";
import { InstructionProps } from "./Instruction";

interface SleeveInstructionProps extends InstructionProps {
  centerChart: { sts: number; row: number };
}

export default function SleeveInstruction({
  schema,
  measurement,
  centerChart,
}: SleeveInstructionProps) {
  const startSts = schema.total.sleeve;
  const { decRows, finalDec } = schema.sleeve;

  const chartStartRow = finalDec.row - centerChart.row;

  let stsCountAtChartStart = startSts;
  for (let i = 0; i < decRows.length; i++) {
    if (decRows[i] + 1 <= chartStartRow) {
      stsCountAtChartStart -= 2;
    }
  }

  const chartStartPoint = Math.floor(
    (stsCountAtChartStart - centerChart.sts) / 2
  );

  // final decrease on

  return (
    <>
      <SectionInstruction
        intro="ðŸª¡ Move the sts in scrap yarn or stitch holder to US 11 / 7mm needle."
        rowType="Pick up"
        rows={[
          {
            text: `underarm ${schema.underarm} sts`,
            notes: `sleeve total ${startSts} sts`,
          },
        ]}
        afterList={[
          `Pm after ${Math.round(
            schema.underarm / 2
          )} sts - this is BOR of sleeve. Rnd 1 start here.`,
        ]}
        notes={`Following row numbers are based on the sleeve length from underarm, ${getInchAndCm(
          measurement.sleeve_length_from_underarm
        )}.`}
      />
      <SectionInstruction
        rowType="Next rnd"
        rows={[{ text: `k${startSts}` }]}
        notes={`Rep the prev row to approx ${getInchAndCm(2)}.`}
      />
      <SectionInstruction
        title="Taper sleeve"
        intro="Dec 1 at each side of marker across two rnds"
        rowType="Rnd"
        startRowID={decRows[0] - 1}
        rows={[
          { text: "k till 2 sts bef marker, k2tog" },
          {
            text: `k2, ssk, k till marker`,
            notes: `2 sts dec, ${startSts - 2} sts remain`,
          },
        ]}
        notes={`rep at row ${decRows
          .slice(1)
          .map((row) => `${row}/${row + 1}`)
          .join(", ")} (${startSts - decRows.length * 2} sts remain)`}
      />
      <SectionInstruction
        title="Chart A before cuff"
        intro={`While you work taper sleeve dec, start chart A at row ${chartStartRow}.`}
        beforeList={[
          `After dec, ${stsCountAtChartStart} sts left.`,
          `From BOR, pm A at ${chartStartPoint} sts (mA is the beg of chart A.)`,
          `From A, pm B at ${centerChart.sts} sts (mB is the end of chart A.)`,
        ]}
        rowType="Rnd"
        startRowID={chartStartRow - 1}
        rows={[{ text: `k till mA, smA, work chart B, smB, k till BOR` }]}
      />
      <SectionInstruction
        title="Final decrease"
        rowType="Rnd"
        startRowID={finalDec.row - 1}
        rows={[
          {
            text: `dec ${finalDec.sts} sts around the sleeve.`,
            notes: `${finalDec.sts} dec'd, ${
              stsCountAtChartStart - finalDec.sts
            } sts left`,
          },
          { text: `Change to smaller needle, k all` },
        ]}
      />
      <SectionInstruction
        title="Hem 1x1 ribbing"
        rowType="Rnd"
        startRowID={finalDec.row + 1}
        rows={[{ text: `(k1, p1) repeat (-) till BOR` }]}
        notes={`Rep the prev row to approx ${getInchAndCm(
          measurement.cuff_length
        )}.`}
      />
    </>
  );
}
